import "./App.css";
import { BrowserRouter } from "react-router-dom";

import Navigation from "./layouts/Navigation";
import Content from "./layouts/Content";
import Footer from "./layouts/Footer";

// Importowanie kontekstu, globalnego stanu aplikacji
import { AppContext } from "./context/appContext";
import { useState } from "react";

function App() {
  // Stan komponentu przypisany do globalnego stanu aplikacji obsugujcy paginacj.
  // Dla wymagania utrzymywania stanu podczas przechodzenia pomidzy zakadkami.
  // Dla maych aplikacji idealne rozwizanie, w tym przypadku lepsze ni Redux.
  const [usersActualPage, setUsersActualPage] = useState(1);
  const [postsActualPage, setPostsActualPage] = useState(1);
  const usersSetPage = (value) => setUsersActualPage(value);
  const postsSetPage = (value) => setPostsActualPage(value);

  // Stan przypisany do kontekstu aplikacji i jego obsuga odpowiedzialne za utrzymywanie stanu nt wyrenderowanych postw
  const [renderedPosts, setRenderedPosts] = useState([]);
  const setRenderedPostsGlobal = (value) => setRenderedPosts(value);
  // Stan przypisany do kontekstu aplikacji i jego obsuga odpowiedzialne za utrzymywanie stanu nt wyrenderowanych uytkownikw
  const [renderedUsers, setRenderedUsers] = useState([]);
  const setRenderedUsersGlobal = (value) => setRenderedUsers(value);
  // Stan przypisany do kontekstu aplikacji i jego obsuga odpowiedzialna za utrzymywanie stanu nt wyrenderowanej listy zada. Nie pobiera ponownie pobranych ju wczeniej zada
  const [renderedTodos, setRenderedTodos] = useState([]);
  const setRenderedTodosGlobal = (value) => setRenderedTodos(value);

  return (
    <AppContext.Provider
      value={{
        usersActualPage,
        usersSetPage,
        postsActualPage,
        postsSetPage,
        renderedPosts,
        setRenderedPostsGlobal,
        renderedUsers,
        setRenderedUsersGlobal,
        renderedTodos,
        setRenderedTodosGlobal,
      }}
    >
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Navigation />
        <Content />
        <Footer />
      </BrowserRouter>
    </AppContext.Provider>
  );
}

export default App;
