// Komponent odpowiedzialny za renderowanie listy zada

import React, {useEffect, useRef, useContext } from "react";

// Import biblioteki styled-components do stylowania elementw
import styled from "styled-components";

// Import komponentw
import Inventive from "../components/Incentive";
import TodosRecord from "../components/TodosRecord";
import TodosRecordTitle from "../components/TodosRecordTitle";

// Import komponentw z biblioteki react-virtualized niezbdnych do poprawnego dziaania listy
import {
  InfiniteLoader,
  List,
  AutoSizer,
  CellMeasurer,
  CellMeasurerCache,
} from "react-virtualized";
import "react-virtualized/styles.css"; // only needs to be imported once

// Importowanie kontekstu do obsugi globalnego stanu aplikacji
import { AppContext } from "../context/appContext";

// Import grafiki dla tego komponentu
import tasks from "../images/tasks.png";

const DisplayedList = styled.div`
  height: 500px;

  .list-of-tasks {
    scrollbar-width: thin;
  }
`;

const Todos = () => {
  // Obiekt reprezentujcy waciwoci do wywietlenia w sekcji powitalnej danej strony
  // Przekazywany jest do osobnego komponentu ze wzgldu, ktry renderuje zawarto dla danej podstrony
  // Rozwizanie uyte z powodu na powtarzalno wykonania tego samego kodu
  const infoAboutThisPage = {
    name: "Todos",
    headerTitle: "Tasks to do downloaded from GoREST",
    additionalInfo: "On this page You can check tasks to do downloaded from ",
    btnText: "",
    btnFunction: "",
    btnAdditionalInfo: "Check actual tasks below",
    image: tasks,
  };

  const { renderedTodos, setRenderedTodosGlobal } = useContext(AppContext); // Stan dla aktualnie wybranej strony pobrany z kontekstu aplikacji (globalnego stanu).

  // Po zamontowaniu tego komponentu (Todos) wykonywany jest useEffect, ktry pobiera dane dla strony nr 1 z API.
  useEffect(() => {
    const url = `https://gorest.co.in/public/v2/todos?page=${1}`;
     if(renderedTodos.length === 0){
      fetch(url)
      .then((response) => {
        if (response.status !== 200) {
          throw Error("Error with downloading todos data ");
        } else {
          return response.json(); // Dane konwertowane na obiekt JSON
        }
      })
      .then((response) => setRenderedTodosGlobal(response)) // Przypisanie pobranych taskw ze strony nr 1 API do globalnego stanu. Na tej podstawie zostanie wyrenderowana lista na pocztku.

      // Przechwycenie bdu gdy promise (fetch) zostanie odrzucony
      .catch((error) => console.log(error));
     }

  }, []); // pusta tablica zalenoci sprawia, e useEffect wykona si tylko raz niezalene od zmiany stanu komponentu poniewa tablica zalenoci jest pusta.

  // ReactVirtualized: wartoci niezbdne do automatycznego dostosowywania rozmiarw elementw listy. Waciwoci niezbdne dla React Virtualized
  const cache = useRef(
    new CellMeasurerCache({
      fixedWidth: true,
      defaultHeight: 100,
    })
  );

  // ReactVirtualized: funkcja sprawdzajca czy zosta pobrany dany element
  const isRowLoaded = ({ index }) => {
    return !!renderedTodos[index];
  };

  // ReactVirtualized: funkcja obsugujca dodawanie kolejnych danych (taskw)
  const loadMoreRows = ({ startIndex, stopIndex }) => {
    const page = startIndex / 10 + 1; // Domylnie zmienna startIndex w ReactVirtualized wynosi 0 a nastpnie zwiksza o 10. Tutaj jednak odwoujemy si do konkretnej strony poprzez API zatem interesuj nas strony (1,2,3...)
    const url = `https://gorest.co.in/public/v2/todos?page=${page}`;
    return (
      fetch(url)
        .then((response) => {
          if (response.status !== 200) {
            throw Error("Bd zapytania");
          } else {
            // Pobieramy z headera list wszystkich podstronnpm dla paginacji
            return response.json(); // Dane konwertowane na obiekt JSON
          }
        })
        .then((response) => {
          if (page > 1) { // Dopiero gdy nasza aplikacja chce pobra stron wiksz ni strona nr 1 (ktra jest domylnie pobierana po zamontowaniu komponentu w useEffect) pobrane wartoci zostaj dopisane do stanu reprezentujcego tablic taskw
            if ( // Zabezpieczenie przeciw duplikacji tych samych danych. Jeli ostatni element tablicy = ostatniemu elementowi response oznacza to bdzie, e pobrana zostaa ponownie ta sama strona - czyli tych danych nie powinno si dopisa do stanu. Uywane rwnie w przypadku ponownego renderowania komponentu. 
              renderedTodos[renderedTodos.length - 1].id !==
              response[response.length - 1].id
            ) {
              setRenderedTodosGlobal((prevValue) => prevValue.concat(response));
            }
          }
        })

        // Przechwycenie bdu gdy promise (fetch) zostanie odrzucony
        .catch((error) => console.log(error))
    );
  };

  // ReactVirtualized: funkcja renderujca pojedynczy task na licie
  const rowRenderer = ({ key, index, style, parent }) => {

    return (
      <CellMeasurer
        key={key}
        cache={cache.current}
        parent={parent}
        rowIndex={index}
      >
        <div style={style}>
          <TodosRecord
            key={renderedTodos[index].id}
            task={renderedTodos[index]}
          />
        </div>
      </CellMeasurer>
    );
  };

  return (
    <div data-aos="fade-zoom-in">
      <Inventive infoAboutPage={infoAboutThisPage} />
      <TodosRecordTitle />
      <DisplayedList>
        <AutoSizer>
          {({ height, width }) => (
            <InfiniteLoader
              isRowLoaded={isRowLoaded}
              loadMoreRows={loadMoreRows}
              rowCount={10000}
            >
              {({ onRowsRendered, registerChild }) => (
                <List
                  className="list-of-tasks"
                  height={height}
                  onRowsRendered={onRowsRendered}
                  ref={registerChild}
                  rowCount={renderedTodos.length}
                  rowHeight={cache.current.rowHeight}
                  deferredMeasurementCache={cache.current}
                  rowRenderer={rowRenderer}
                  width={width}
                />
              )}
            </InfiniteLoader>
          )}
        </AutoSizer>
      </DisplayedList>
    </div>
  );
};

export default Todos;
