// Globalny stan aplikacji

import { createContext } from "react";

// Tworzymy obiekt ktry bdzie naszym kontekstem dla aplikacji globalnym stanem. 
export const globalStateApp = {

    // Waciwoci (aktualna wybrana strona paginacji oraz funkcja j obsugujca w komponencie User)
    usersActualPage: 1,
    usersSetPage: () => {},
    
    // Waciwoci (aktualna wybrana strona paginacji oraz funkcja j obsugujca w komponencie Posts)
    postsActualPage: 1,
    postsSetPage: () => {},

    // Waciwoci odpowiedzialne za utrzymanie stanu aktualnie wyrenderowanych postw
    renderedPosts: [],
    setRenderedPostsGlobal: () => {},

    // Waciwoci odpowiedzialne za utrzymanie stanu aktualnie wyrenderowanych uytkownikw
    renderedUsers: [],
    setRenderedUsersGlobal: () => {},

    // Waciwoci odpowiedzialne za utrzymanie stanu aktualnie wyrenderowanych zada
    renderedTodos: [],
    setRenderedTodosGlobal: () => {}
};

// Tworzymy kontekst i go eksportujemy
export const AppContext = createContext(globalStateApp);
