// Komponent odpowiedzialny za renderowanie listy postw

import React, {useState, useContext, useEffect} from "react";

import Inventive from "../components/Incentive";
import Pagination from "../components/Pagination";
import SinglePost from "../components/SinglePost";
import FormForPost from "../components/FormForPost";

// Importowanie kontekstu do obsugi globalnego stanu aplikacji
import { AppContext } from "../context/appContext";

// Import grafiki dla tego komponentu
import posts from "../images/publish-post.png";

const Posts = () => {
  // Obiekt reprezentujcy waciwoci do wywietlenia w sekcji powitalnej danej strony
  // Przekazywany jest do osobnego komponentu ze wzgldu, ktry renderuje zawarto dla danej podstrony
  // Rozwizanie uyte z powodu na powtarzalno wykonania tego samego kodu
  const infoAboutThisPage = {
    name: "Posts",
    headerTitle: "Posts downloaded from GoREST",
    additionalInfo: "On this page You can check posts downloaded from ",
    btnText: "Add post",
    btnFunction: "",
    btnAdditionalInfo: "or check posts below",
    image: posts,
  };

  // Stan i jego obsuga odnoszcy si do widocznoci formularza 
  const [visibleForm, setVisibleForm] = useState(false)
  const handleVisibleForm = () => setVisibleForm(prevValue => !prevValue)

  // Zmienna logiczna suca tylko do wykonania ponownie useEffect, fetch po dodaniu posta aby wywietli nowy post na licie
  const [refreshPage, setRefreshPage] = useState(false)
  const handleRefreshPage = () => setRefreshPage(prevValue => !prevValue)

  // Obsuga globalnego stanu
  const { postsActualPage, postsSetPage, renderedPosts, setRenderedPostsGlobal } = useContext(AppContext) // Stan dla aktualnie wybranej strony (paginacja) oraz funkcja obsugujca jego zmian pobrane z kontekstu aplikacji (globalnego stanu).

  const [totalPages, setTotalPages] = useState(1); // Stan dla totalnej liczby stron w dla paginacji

  useEffect(() => {
    const url = `https://gorest.co.in/public/v2/posts?page=${postsActualPage}`;
    fetch(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        Authorization:
          'Bearer 16e9cc5d10323cf49d5aa6384ec6317bfeeb0582063b2173cabbb09711b4d73f',
      },
    })
      .then((response) => {
        if (response.status !== 200) {
          throw Error("Bd zapytania");
        } else {
          setTotalPages(response.headers.get("x-pagination-pages")); // Pobieramy z headera list wszystkich podstron dla paginacji
          return response.json(); // Dane konwertowane na obiekt JSON
        }
      })
      .then((response) => {
        setRenderedPostsGlobal(response) // Przypisuje do globalnego stanu aktualnie pobrane posty
      })

      // Przechwycenie bdu gdy promise (fetch) zostanie odrzucony
      .catch((error) => console.log(error));
  }, [postsActualPage, refreshPage]); // useEffect wykona si ponownie (pobierze dane) tylko po zamontowaniu kompoenntu pierwszy raz, po zmianie strony (paginacja), po odwieeniu strony (wywoywane rcznie) 


  const PostList = renderedPosts.map(post => (
    <SinglePost key={post.id} data={post}/>
  ))

  const positiveResult = (
    <>
      {PostList}
      <Pagination actualPage={postsActualPage} totalPages={totalPages} handleNumberPage={postsSetPage}/>
    </>
  )

  const loadingDisplay = <p>Loading...</p>
  const renderPostsDecision = renderedPosts.length > 0 ? positiveResult : loadingDisplay; // Jeli lista postw zawiera dane zostan wywietlone, jeli nie wywietli si informacja o adowaniu danych
  const displayForm = visibleForm ? <FormForPost handleReloadData={handleRefreshPage} /> : null; // Decyzja na temat widocznoci formularza

  return (
    <div data-aos="fade-zoom-in">
      <Inventive infoAboutPage={infoAboutThisPage} btnFunction={handleVisibleForm}/>
      {displayForm}
      {renderPostsDecision}
    </div>
  );
};

export default Posts;
